// const _MOVE_2 = 7;


const _INIT_ = [0,1,2,3];
const _ON_ = [4,5,6,7];
const _OFF_ = [8,9,10,11];
const _MOVE_ = [12,13,14,15];


//State label enums


const EPS = 1.0e-6; // lineTo cutoff for proximity of control point to vertex

//TODO: hide control points for most vertices unless they're "parent" vertex
//      is selected
export class PathTool {
  constructor() {
    //this.ops = [];

    //frame map initialization
    this.frame_markers = slider_values;//[0,0.25,0.5,0.75];
    this.frame_pts = [];
    for(var i=0;i<4;i++){
      this.frame_pts.push([]);
    }

    //and a points array for rendering the current frame
    this.points = [];

    this.state = _INIT_;
    this.ctrlPoint = [];
    this.pointsSelected = [];
    this.pointSelected = 0;
    this.hitRadius =  6;
    this.sleep0 = 0;
    this.rnd0 = [];
    this.rnd = [];
    this.actions = [
      {name: "cancel", desc: "cancel path", icon: "icon-cross", key: "Escape"},
      {name: "commit", desc: "start new path", icon: "icon-checkmark", key: "Enter"},
      {name: "back",   desc: "undo last point", icon: "icon-minus", key: "Backspace"},
      {name: "closepath",   desc: "close path", icon: "icon-stroke", key: "KeyC"},
      {name: "smoothclose",   desc: "smooth close path", icon: "icon-radio-unchecked", key: "KeyS"}
    ];
    this.voronoi = gVoronoi;
    this.voronoi_VG = gVoronoi_VG;

    for(var i = 0; i < 10000; i++) {
      this.rnd0[i] = Math.random();
      this.rnd[i] = 0;
    }
  }

  changeMarkers(array){
    this.frame_markers = array.slice();
  }

  generateRandomNumbers(){
    var div = gS.ctxStyle.maxRnd;
    for(var i = 0; i < 10000; i++) {
      this.rnd[i] = this.rnd0[i]*div;
    }
  }
  getLerpPoints(){
    var value = Number(gS.ctxStyle.level);
    const index = this.frame_markers.findIndex((element) => element == value);
    console.log(this.frame_markers);
    if (index!=-1){
      return this.frame_pts.at(index);
    }

    var pts0 ,pts1;
    var key0,key1;
    var pts0Found = false;
    var pts1Found = false;
    for (var i=0; i<4;i++) {
        var key = this.frame_markers.at(i);
        if (value>=key){
          key0 = key;
          pts0 = this.frame_pts.at(i).slice();
          pts0Found = true;
        }else if (pts0Found){
          key1 = key;
          pts1Found = true;
          pts1 = this.frame_pts.at(i).slice(); break;
        }
      }
    if (!pts1Found) pts1 = this.frame_pts.at(0).slice();

      var pts_lerp = [];
      var t = (value-key0)/(key1-key0);
      for(var i=0; i<pts0.length;i++){
        var tmpx = (pts0[i][0])*(1-t) + (pts1[i][0])*(t);
        var tmpy = (pts0[i][1])*(1-t) + (pts1[i][1])*(t);
        pts_lerp.push([tmpx,tmpy]);
      }

      return pts_lerp.slice();
  }

  getPoints(index){
    return this.frame_pts.at(index);
  }

  liverender(render_flag = [false,false,false,false]) {
    lctx.clearRect(0, 0, canvas.width, canvas.height);
    for(var i=0; i<4;i++){
      if (render_flag[i]) lctxs[i].clearRect(0, 0, canvas.width, canvas.height);
    }

    this.points = this.getLerpPoints();
    var frames_pts = [];
    for(var i=0; i<4;i++){
      if (render_flag[i]){
        frames_pts.push(this.getPoints(i));
      }else
        frames_pts.push([]);
    }


    if(this.points.length==0){return;}
    this.line_id = 1;
    const drawOrder = drawKeyToOrderMap[gS.ctxStyle.drawOrder];

    let ptidx = 0;

    for(let drawSet of drawOrder){
      let count = 0;
      for (let af of affineset) {
        count++;
        this.voronoi_VG[0].setNewColor(this.line_id);

        lctx.beginPath();
        let Tpt = af.onVec(this.points[0]);
        lctx.moveTo(Tpt[0], Tpt[1]);

        for(var i=0; i<4;i++) {
          if (render_flag[i]) {
            this.voronoi_VG[i + 1].setNewColor(this.line_id, i+1);
            lctxs[i].beginPath();
            lctxs[i].moveTo(Tpt[0], Tpt[1]);
          }
        }

        let pts = [];
        let ind = (this.line_id * 97 + (ptidx + 1))%10000;
        for(var i = 0; i < this.points.length ; i++){
          let tmp_pt = af.onVec(this.points[i]);
          pts.push(tmp_pt);
        }
        this.voronoi_VG[0].renderCurve(this.line_id, pts);

        for(var i=0; i<4;i++) {
            if (!render_flag[i]) continue;
            pts = [];
            let drawCircles = this.getPoints(i);
            for (var j = 0; j < drawCircles.length; j++) {
              let tmp_pt = af.onVec(drawCircles[j]);
              pts.push(tmp_pt);
            }
            this.voronoi_VG[i + 1].renderCurve(this.line_id, pts);
        }
        if (count==53)  console.log("LINEID = " + this.line_id);
        ptidx+=this.points.length;
        this.line_id += 2;
      }
    }

    for(var i=0; i<4;i++) {
      if (!render_flag[i]) continue;

      var sendPts = [];

      let drawCircles = this.getPoints(i);
      for (var j = 0; j < drawCircles.length; j++) {
        let pt0 = drawCircles[j];
        sendPts.push(new THREE.Vector2(pt0[0], pt0[1]));
        drawHitCircle(lctxs[i], pt0[0], pt0[1], this.hitRadius);
      }
      drawSpline_pts(lctxs[i], sendPts);
    }

  }

  mouseDown1(e) {
    this.points = this.getPoints(0);

    let rect = liveCanvas[0].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    this.state = _ON_[0];
    let onPoint = false;
    let i = 0;
    while(i<this.points.length){
      let currPt = this.points[i];
      if (l2dist(currPt,pt)<this.hitRadius){
        onPoint = true;
        this.state = _MOVE_[0];
        this.pointSelected = i;
        break;
      }
      i+=1;
    }
    if (!onPoint){
      this.points.push(pt);
      this.frame_pts[0] = this.points.slice();
      var changedInd = [true,false,false,false];
      for (var j=0; j<4;j++) {
        if (this.frame_pts[j].length<this.points.length){
          this.frame_pts[j].push(pt);
          changedInd[j] = true;
        }

      }
      this.liverender(changedInd);
    }

  }

  mouseMove1(e) {
    this.points = this.getPoints(0);
    let rect = liveCanvas[0].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[0]){
      this.points[this.pointSelected] = pt;
      this.frame_pts[0] = this.points.slice();
      var changedInd = [true,false,false,false];
      this.liverender(changedInd);
    }
  }

  mouseUp1(e) {
    this.points = this.getPoints(0);
    let rect = liveCanvas[0].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[0]){
      this.points[this.pointSelected] = pt;
      this.state = _ON_[0] ;
      this.frame_pts[0] = this.points.slice();
      var changedInd = [true,false,false,false];
      this.liverender(changedInd);
    }
  }


  //------------------------

  mouseDown2(e) {
    this.points = this.getPoints(1);

    let rect = liveCanvas[1].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    this.state = _ON_[1];
    let onPoint = false;
    let i = 0;
    while(i<this.points.length){
      let currPt = this.points[i];
      if (l2dist(currPt,pt)<this.hitRadius){
        onPoint = true;
        this.state = _MOVE_[1];
        this.pointSelected = i;
        break;
      }
      i+=1;
    }
    if (!onPoint){
      this.points.push(pt);
      this.frame_pts[1] = this.points.slice();
      var changedInd = [false,true,false,false];
      for (var j=0; j<4;j++) {
        if (this.frame_pts[j].length<this.points.length){
          this.frame_pts[j].push(pt);
          changedInd[j] = true;
        }
      }
      this.liverender(changedInd);
    }

  }

  mouseMove2(e) {
    this.points = this.getPoints(1);
    let rect = liveCanvas[1].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[1]){
      this.points[this.pointSelected] = pt;
      this.frame_pts[1] = this.points.slice();
      var changedInd = [false,true,false,false];
      this.liverender(changedInd);
    }
  }

  mouseUp2(e) {
    this.points = this.getPoints(1);
    let rect = liveCanvas[1].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[1]){
      this.points[this.pointSelected] = pt;
      this.state = _ON_[1] ;
      this.frame_pts[1] = this.points.slice();
      var changedInd = [false,true,false,false];
      this.liverender(changedInd);
    }
  }


  mouseDown3(e) {
    this.points = this.getPoints(2);

    let rect = liveCanvas[2].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    this.state = _ON_[2];
    let onPoint = false;
    let i = 0;
    while(i<this.points.length){
      let currPt = this.points[i];
      if (l2dist(currPt,pt)<this.hitRadius){
        onPoint = true;
        this.state = _MOVE_[2];
        this.pointSelected = i;
        break;
      }
      i+=1;
    }
    if (!onPoint){
      this.points.push(pt);
      this.frame_pts[2] = this.points.slice();
      var changedInd = [false,false,true,false];
      for (var j=0; j<4;j++) {
        if (this.frame_pts[j].length<this.points.length){
          this.frame_pts[j].push(pt);
          changedInd[j] = true;
        }
      }
      this.liverender(changedInd);
    }

  }

  mouseMove3(e) {
    this.points = this.getPoints(2);
    let rect = liveCanvas[2].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[2]){
      this.points[this.pointSelected] = pt;
      this.frame_pts[2] = this.points.slice();
      var changedInd = [false,false,true,false];
      this.liverender(changedInd);
    }
  }

  mouseUp3(e) {
    this.points = this.getPoints(2);
    let rect = liveCanvas[2].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[2]){
      this.points[this.pointSelected] = pt;
      this.state = _ON_[2] ;
      this.frame_pts[1] = this.points.slice();
      var changedInd = [false,false,true,false];
      this.liverender(changedInd);
    }
  }

  mouseDown4(e) {
    this.points = this.getPoints(3);

    let rect = liveCanvas[3].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    this.state = _ON_[3];
    let onPoint = false;
    let i = 0;
    while(i<this.points.length){
      let currPt = this.points[i];
      if (l2dist(currPt,pt)<this.hitRadius){
        onPoint = true;
        this.state = _MOVE_[3];
        this.pointSelected = i;
        break;
      }
      i+=1;
    }
    if (!onPoint){
      this.points.push(pt);
      this.frame_pts[3] = this.points.slice();
      var changedInd = [false,false,false,true];
      for (var j=0; j<4;j++) {
        if (this.frame_pts[j].length<this.points.length){
          this.frame_pts[j].push(pt);
          changedInd[j] = true;
        }
      }
      this.liverender(changedInd);
    }

  }

  mouseMove4(e) {
    this.points = this.getPoints(3);
    let rect = liveCanvas[3].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[3]){
      this.points[this.pointSelected] = pt;
      this.frame_pts[3] = this.points.slice();
      var changedInd = [false,false,false,true];
      this.liverender(changedInd);
    }
  }

  mouseUp4(e) {
    this.points = this.getPoints(3);
    let rect = liveCanvas[3].getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_[3]){
      this.points[this.pointSelected] = pt;
      this.state = _ON_[3] ;
      this.frame_pts[3] = this.points.slice();
      var changedInd = [false,false,false,true];
      this.liverender(changedInd);
    }
  }

  keyDown(e) {
    if(e.target.type){return;} // don't interfere with input UI key-events
    for(let action of this.actions){
      if(action.key == e.code){
        this[action.name]();
      }
    }
  }


}



